import React, { useState, useRef } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import getAvatar from '../../utils/getAvatar'
import LoginModal from './LoginModal'
import * as cll from './Layout.module.css'

import {
    AppBar,
    IconButton,
    Slide,
    Toolbar,
    useScrollTrigger,
    createStyles,
    makeStyles,
    Theme,
    Container,
    Badge,
    Avatar,
    LinearProgress,
    Dialog,
    useMediaQuery,
    Box,
    Paper,
    Typography,
    Button,
} from '@material-ui/core'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialIcon from '@material-ui/lab/SpeedDialIcon'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'
import {
    Book,
    GifTwoTone,
    HomeRounded,
    Launch,
    Notifications,
    Person,
    Star,
} from '@material-ui/icons'
import DialogFullscreen from '../UI/DialogFullscreen'
import AddFriends from '../../containers/Friends/AddFriends'
import LanguageSwitcher from '../LanguageSwitcher/LanguageSwitcher'
import { useTranslation } from 'react-i18next'
import EditResource from '../EditResource/EditResource'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        speedDial: {
            right: '12px',
            // bottom: theme.spacing(2),
            bottom: '16px',
            position: 'fixed',
        },
        logout: {
            left: theme.spacing(2),
            bottom: theme.spacing(2),
            position: 'fixed',
        },
        login: {
            right: '16px',
            bottom: '16px',
            position: 'fixed',
        },
        home: {
            right: '16px',
            bottom: '88px',
            position: 'fixed',
        },
        avatar: {
            border: '1px solid ' + theme.palette.primary.main,
        },
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
        blogLabel: {
            margin: theme.spacing(0, 0.5, 0, 1),
            display: 'none',
            [theme.breakpoints.up('sm')]: {
                display: 'inline',
            },
        },
        blogIcon: {
            display: 'inline',
            [theme.breakpoints.up('sm')]: {
                display: 'none',
            },
        },
    })
)

const TransitionComponent = props => <Slide direction="up" {...props} />

const AccountLayout = props => {
    const { t } = useTranslation()
    const domRef = useRef()
    const matches = useMediaQuery(theme => theme.breakpoints.up('sm'))
    const { account, language } = props

    const [loginVisible, setLoginVisible] = useState(false)
    const [startResourceVisible, setStartResourceVisible] = useState('')
    const [addFriendOpen, setAddFriendOpen] = useState(false)
    const [speedDialOpen, setSpeedDialOpen] = useState(false)

    const openLink = url => props.history.push(url)

    const isActivating = props.history.location.pathname === '/activate-account'

    let numberOfUnseenNots = 0

    for (let not of props.lastNotifications) {
        if (parseInt(not.notId) > parseInt(account.lastSeenNot || 0))
            numberOfUnseenNots++
    }
    const logedIn = props.isAuthed && account._id

    const classes = useStyles()
    const scrollTrigger = !useScrollTrigger()
    const checkPagePath = paths => {
        for (let path of paths)
            if (props.history.location.pathname.indexOf(path) === 0) return true
    }
    const removeGutters =
        !matches && checkPagePath(['/goals', '/posts', '/profile', '/account'])
    return props.hide ? (
        props.children
    ) : (
        <>
            <div
                className={cll.Layout}
                style={props.white ? { background: '#fff' } : {}}
            >
                <Slide appear={false} direction="down" in={scrollTrigger}>
                    <AppBar elevation={1} color="secondary">
                        {props.isConnectingWS && !logedIn && (
                            <LinearProgress
                                style={{
                                    width: '100%',
                                    position: 'absolute',
                                    top: 0,
                                    left: 0,
                                }}
                            />
                        )}

                        <Toolbar>
                            <div
                                tabIndex={0}
                                ref={domRef}
                                style={{ width: 0, height: 0 }}
                            />
                            {/* <div className={classes.MenuButtonContiner}>
                                <IconButton
                                    onClick={() => props.history.goBack()}
                                >
                                    <ArrowBack color="primary" />
                                </IconButton>
                            </div> */}
                            {(account._id && props.header) || (
                                <Box style={{ display: 'flex', flex: 1 }}>
                                    <Box style={{ display: 'flex', flex: 1 }}>
                                        <Button component={Link} to={'/'}>
                                            <img
                                                style={{
                                                    height: '100%',
                                                    position: 'absolute',
                                                    left: 0,
                                                }}
                                                src="/android-icon-36x36.png"
                                            />
                                            <img
                                                style={{
                                                    height: '18px',
                                                    visibility: 'hidden',
                                                }}
                                                src="/android-icon-36x36.png"
                                            />
                                            <Typography
                                                variant="button"
                                                className={classes.blogLabel}
                                            >
                                                Addspire
                                            </Typography>
                                        </Button>
                                    </Box>
                                    <Box style={{ display: 'flex' }}>
                                        <LanguageSwitcher />

                                        <Button
                                            component={Link}
                                            to={`/${
                                                language !== 'en'
                                                    ? language + '/'
                                                    : ''
                                            }blog`}
                                            style={{ marginRight: '20px' }}
                                        >
                                            <Book
                                                className={classes.blogIcon}
                                            />
                                            <span className={classes.blogLabel}>
                                                {t('layout.blog')}
                                            </span>
                                        </Button>

                                        {(logedIn && isActivating) ||
                                        (!logedIn && props.isConnectingWS) ? (
                                            <Button
                                                onClick={() =>
                                                    props.history.push(
                                                        '/logout'
                                                    )
                                                }
                                            >
                                                {t('g.logout')}
                                            </Button>
                                        ) : (
                                            !account._id && (
                                                <Button
                                                    variant="contained"
                                                    color="primary"
                                                    // startIcon={<Person />}
                                                    onClick={() =>
                                                        setLoginVisible(true)
                                                    }
                                                >
                                                    {t('layout.login')}
                                                </Button>
                                            )
                                        )}
                                    </Box>
                                </Box>
                            )}
                        </Toolbar>
                    </AppBar>
                </Slide>
                <Toolbar id="back-to-top-anchor" />
                <Container
                    style={{ paddingTop: '20px', paddingBottom: '20px' }}
                    disableGutters={Boolean(removeGutters)}
                >
                    {props.children}
                </Container>

                {/* <Toolbar
                    style={{
                        marginTop: '50px',
                        background: '#eee',
                        paddingTop: '22px',
                        paddingBottom: '22px',
                    }}
                >
                    <Box style={{ display: 'flex', flex: 1 }}>
                        <Box
                            style={{
                                display: 'flex',
                                flex: 1,
                                justifyContent: 'center',
                            }}
                        >
                            <Link
                                to="/"
                                style={{
                                    display: 'flex',
                                    alignItems: 'baseline',
                                }}
                            >
                                <img
                                    style={{
                                        height: '23px',
                                    }}
                                    src="/android-icon-36x36.png"
                                />
                                <Typography variant="button">
                                    Addspire
                                </Typography>
                            </Link>
                        </Box>
                    </Box>
                </Toolbar> */}

                {logedIn && !isActivating && (
                    <SpeedDial
                        ariaLabel="SpeedDial"
                        className={classes.speedDial}
                        icon={<SpeedDialIcon />}
                        onClose={(event, reason) => {
                            if (reason !== 'mouseLeave') {
                                domRef.current.focus()
                                setSpeedDialOpen(false)
                            }
                        }}
                        onOpen={(event, reason) =>
                            reason === 'toggle' && setSpeedDialOpen(true)
                        }
                        open={speedDialOpen}
                    >
                        <SpeedDialAction
                            icon={<Person />}
                            tooltipTitle={
                                <span
                                    style={{
                                        whiteSpace: 'nowrap',
                                    }}
                                >
                                    {t('g.addFriend')}
                                </span>
                            }
                            tooltipOpen
                            onClick={() => {
                                domRef.current.focus()
                                setAddFriendOpen(true)
                                setSpeedDialOpen(false)
                            }}
                        />
                        <SpeedDialAction
                            icon={<Star />}
                            tooltipTitle={
                                <span
                                    style={{
                                        whiteSpace: 'nowrap',
                                    }}
                                >
                                    {t('g.addGoal')}
                                </span>
                            }
                            tooltipOpen
                            onClick={() => {
                                domRef.current.focus()
                                setStartResourceVisible('goal')
                                setSpeedDialOpen(false)
                            }}
                        />
                        <SpeedDialAction
                            icon={<Launch />}
                            tooltipTitle={
                                <span
                                    style={{
                                        whiteSpace: 'nowrap',
                                    }}
                                >
                                    {t('g.addActivity')}
                                </span>
                            }
                            tooltipOpen
                            onClick={() => {
                                domRef.current.focus()
                                setStartResourceVisible('activity')
                                setSpeedDialOpen(false)
                            }}
                        />
                        <SpeedDialAction
                            icon={<Star />}
                            tooltipTitle={
                                <span
                                    style={{
                                        whiteSpace: 'nowrap',
                                    }}
                                >
                                    {t('g.addReward')}
                                </span>
                            }
                            tooltipOpen
                            onClick={() => {
                                domRef.current.focus()
                                setStartResourceVisible('reward')
                                setSpeedDialOpen(false)
                            }}
                        />
                    </SpeedDial>
                )}
                {(logedIn || !props.isConnectingWS) &&
                    (isActivating && 'Activate your account')}

                <Slide appear={false} direction="up" in={!useScrollTrigger()}>
                    <Box
                        style={{
                            position: 'fixed',
                            bottom: '12px',
                            width: '100%',
                        }}
                    >
                        {logedIn && !isActivating && (
                            <Box
                                style={{
                                    display: 'flex',
                                    justifyContent: 'center',
                                }}
                            >
                                <Paper
                                    elevation={3}
                                    style={{ borderRadius: '37px' }}
                                >
                                    <IconButton
                                        onClick={() => openLink('/')}
                                        color={
                                            props.history.location.pathname ===
                                            '/'
                                                ? 'primary'
                                                : 'default'
                                        }
                                    >
                                        <HomeRounded fontSize="large" />
                                    </IconButton>
                                    <IconButton
                                        onClick={() => openLink('/posts')}
                                        color={
                                            props.history.location.pathname ===
                                            '/posts'
                                                ? 'primary'
                                                : 'default'
                                        }
                                    >
                                        <Badge
                                            badgeContent={numberOfUnseenNots}
                                            color="secondary"
                                        >
                                            <Notifications fontSize="large" />
                                        </Badge>
                                    </IconButton>
                                    <IconButton
                                        onClick={() =>
                                            openLink('/profile/' + account._id)
                                        }
                                    >
                                        <Badge
                                            badgeContent={
                                                account.friends.filter(
                                                    item =>
                                                        item.status ===
                                                        'inviting'
                                                ).length
                                            }
                                            color="secondary"
                                        >
                                            <Avatar
                                                classes={
                                                    props.history.location
                                                        .pathname ===
                                                    '/profile/' + account._id
                                                        ? {
                                                              root:
                                                                  classes.avatar,
                                                          }
                                                        : {}
                                                }
                                                src={getAvatar(
                                                    account._id,
                                                    account.image
                                                )}
                                            />
                                        </Badge>
                                    </IconButton>
                                </Paper>
                            </Box>
                        )}
                    </Box>
                </Slide>
            </div>
            <LoginModal
                loginVisible={loginVisible}
                setLoginVisible={setLoginVisible}
            />
            {(logedIn || !props.isConnectingWS) && (!isActivating && <></>)}

            {account._id && (
                <>
                    <DialogFullscreen
                        open={addFriendOpen}
                        close={() => {
                            setAddFriendOpen(false)
                        }}
                        title={t('g.addFriend')}
                    >
                        <AddFriends
                            close={() => setAddFriendOpen(false)}
                            open={addFriendOpen}
                            account={account}
                            friendData={props.friendData}
                        />
                    </DialogFullscreen>

                    <Dialog
                        fullScreen
                        open={Boolean(startResourceVisible)}
                        onClose={() => {
                            setStartResourceVisible('')
                        }}
                        TransitionComponent={TransitionComponent}
                    >
                        <EditResource
                            account={account}
                            friendData={props.friendData}
                            cancel={() => setStartResourceVisible('')}
                            type={startResourceVisible}
                        />
                    </Dialog>
                </>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
        lastNotifications: state.lastNotifications,
        isConnectingWS: state.isConnectingWS,
        friendData: state.friendData,
        language: state.language,
    }
}

export default withRouter(connect(mapStateToProps)(AccountLayout))
